import React from "react";
import { useForm } from "react-hook-form";

interface FormGeneratorProps {
  schema: any;
}

const FormGenerator: React.FC<FormGeneratorProps> = ({ schema }) => {
  const { register, handleSubmit, formState: { errors } } = useForm();

  const onSubmit = (data: any) => {
    console.log("Form Submitted:", data);
    alert("Form submitted successfully!");
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
      <h1 className="text-2xl font-bold">{schema.formTitle}</h1>
      <p className="text-gray-600">{schema.formDescription}</p>
      {schema.fields.map((field: any) => {
        switch (field.type) {
          case "text":
          case "email":
          case "password":
            return (
              <div key={field.id}>
                <label htmlFor={field.id} className="block font-semibold">
                  {field.label}
                </label>
                <input
                  id={field.id}
                  type={field.type}
                  placeholder={field.placeholder}
                  className="w-full p-2 border rounded"
                  {...register(field.id, { required: field.required })}
                />
                {errors[field.id] && (
                  <p className="text-red-500 text-sm">This field is required</p>
                )}
              </div>
            );
          case "textarea":
            return (
              <div key={field.id}>
                <label htmlFor={field.id} className="block font-semibold">
                  {field.label}
                </label>
                <textarea
                  id={field.id}
                  placeholder={field.placeholder}
                  className="w-full p-2 border rounded"
                  {...register(field.id, { required: field.required })}
                />
                {errors[field.id] && (
                  <p className="text-red-500 text-sm">This field is required</p>
                )}
              </div>
            );
          case "select":
            return (
              <div key={field.id}>
                <label htmlFor={field.id} className="block font-semibold">
                  {field.label}
                </label>
                <select
                  id={field.id}
                  className="w-full p-2 border rounded"
                  {...register(field.id, { required: field.required })}
                >
                  {field.options.map((option: any) => (
                    <option key={option.value} value={option.value}>
                      {option.label}
                    </option>
                  ))}
                </select>
                {errors[field.id] && (
                  <p className="text-red-500 text-sm">This field is required</p>
                )}
              </div>
            );
          default:
            return null;
        }
      })}
      <button
        type="submit"
        className="bg-blue-500 text-white py-2 px-4 rounded"
      >
        Submit
      </button>
    </form>
  );
};

export default FormGenerator;
