import React, { useState } from "react";
import Editor from "./components/Editor";
import FormGenerator from "./components/FormGenerator";
import ValidationPreview from "./components/ValidationPreview";
import DarkModeToggle from "./components/DarkModeToggle";
import { parseJSONSchema } from "./utils/schemaParser";
import "./main.css";


const App: React.FC = () => {
  const [schema, setSchema] = useState<string>("{}");
  const [parsedSchema, setParsedSchema] = useState<any>(null);
  const [error, setError] = useState<string | null>(null);

  const handleSchemaChange = (value: string) => {
    setSchema(value);
    try {
      const parsed = JSON.parse(value); // Parse the JSON input
      const processedSchema = parseJSONSchema(parsed); // Process schema
      setParsedSchema(processedSchema);
      setError(null); // Clear errors on success
    } catch (err) {
      setError("Invalid JSON format or schema.");
      setParsedSchema(null); // Reset parsed schema on error
    }
  };

  return (
    <div className="dark:bg-gray-900 dark:text-gray-100 min-h-screen">
      <header className="p-4 border-b">
        <h1 className="text-xl font-bold">Dynamic Form Generator</h1>
        <DarkModeToggle />
      </header>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 p-4">
        {/* Editor and Validation Preview */}
        <div>
          <Editor schema={schema} onChange={handleSchemaChange} error={error} />
          {parsedSchema && (
            <ValidationPreview schema={parsedSchema} />
          )}
        </div>

        {/* Form Generator */}
        <div>
          {parsedSchema ? (
            <FormGenerator schema={parsedSchema} />
          ) : (
            <p className="text-gray-500">Enter a valid JSON schema to generate the form.</p>
          )}
        </div>
      </div>
    </div>
  );
};

export default App;
